* {
  margin: 0 auto;
  padding: 0;
  width: 1260px;
  text-align: center;
}

img {
  width: 420px;
  height: 420px;
}

h1 {
  background-color: #333;
  color: #fff;
  text-align: center;
}

div > ul {
  list-style: none;
  height: 40px;
}

.main {
  position: relative;
  background-color: #964b00;
}

.main > li {
  display: inline-block;
  position: relative;
  width: 400px;
  text-align: center;
  line-height: 40px;
  letter-spacing: 0.05em;
}

.main a {
  text-decoration: none;
  color: #fff;
}

.main .active {
  color: #000;
}

.screen, .expected {
  height: 800px;
  position: relative;
  display: inline-block;
}

.screen li, .expected li {
  display: inline-block;
  position: relative;
  line-height: 40px;
  letter-spacing: 0.05em;
  width: 420px;
  height: 420px;
}

.screen a, .expected a {
  position: relative;
  width: 420px;
  height: 420px;
}

.screen a p, .expected a p {
  position: relative;
  display: block;
  left: -420px;
  top: -20px;
}

.cont {
  position: relative;
  display: inline-block;
  color: #fff;
}
